<template>
  <div
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
    ref="chartEl"
    :style="{width:params.canvasStyle.width,height:params.canvasStyle.height}"
  ></div>
</template>
<script>
  import echarts from "echarts";
  import "echarts-liquidfill";

  export default {
    name: "liquidfill",
    data() {
      return {
        myChart: null
      };
    },
    props: {
      params: {
        type: Object
      }
    },
    mounted() {
      this.init();
    },
    methods: {
      init() {

        if (this.myChart) {
          this.myChart.dispose();
          this.myChart = null;
        }
        if (this.params.data.length === 0) return;        
        let value = ((this.params.data[0].value)*100).toFixed(2)/100;
        // 基于准备好的dom，初始化echarts实例
        this.myChart = echarts.init(this.$refs.chartEl);
        this.myChart.setOption({
          title: {
            text: this.params.title ? this.params.title : "",
            textStyle: {
              color: "#13f0ff",
              fontSize: "20%"
            },
            x: "center",
            y: "bottom"
          },
          series: [
            {
              type: "liquidFill",
              color: [
                {
                  type: "linear",
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [
                    {
                      offset: 0,
                      color: "#ffe97d" // 0% 处的颜色
                    },
                    {
                      offset: 1,
                      color: "#ff957c" // 100% 处的颜色
                    }
                  ],
                  global: false // 缺省为 false
                }
              ],
              radius: this.params.options.radius ? this.params.options.radius : "50%",
              outline: {
                show: true,
                borderDistance: 8,
                itemStyle: {
                  color: "transparent",
                  borderColor: "rgba(255,233,125,0.3)",
                  borderWidth: 10,
                  shadowBlur: 0
                }
              },
              label: {
                normal: {
                  show: true,
                  color: this.$STANDARD.liquidLabelColor,
                  insideColor: this.$STANDARD.liquidLabelColor,//波纹处文字颜色
                  fontSize: 32,
                  fontWeight: 700,
                  align: "center",
                  baseline: "middle",
                  position: "inside",
                  formatter: params => {
                    if (params.value) {
                      return `${(params.value * 100).toFixed(2)}%`
                    } else {
                      return `0%`;
                    }
                  }
                }
              },
              backgroundStyle: {
                color: "#2d4972"
              },
              data: [value]
            }
          ]
        });
      }
    },
    watch: {
      'params.data': {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      }
    }
  };
</script>

<style lang="stylus" scoped></style>
